import './NumButtons.css'
import React from 'react'
class NumButtons extends React.Component {
    constructor (props){
        super(props)
        this.write=this.write.bind(this)
    }
    write() {
        document.querySelector('.input').value += this.props.number
        console.log(document.querySelector('.input').value)
    }
    render () {
        return (
            <div  onClick={this.write} className="display inNumbers" id={this.props.id} key={this.props.id} style={{width:'60px', cursor:'pointer', height:'60px'}}>{this.props.number}</div>
        )
    }
}

export default NumButtons;