import "./Input.css"
import React from "react";
import { type } from "@testing-library/user-event/dist/type";
function Input(){
    function check(){
        let x=document.querySelector('.input').value;
        let lastInput=x.substr(x.length-1)
        console.log(lastInput)
       if (lastInput != '1' && lastInput != '2' && lastInput != '3' && lastInput != '4' && lastInput != '5' && lastInput != '6' && lastInput != '7' && lastInput != '8' && lastInput != '9' && lastInput != '0' && lastInput != '+' && lastInput != '-' && lastInput != '/' && lastInput != '*' && lastInput != '=' && lastInput != ''){
           alert('No letters allowed')
           document.querySelector('.input').value=x.slice(0, -1);
       }
        if(lastInput ==  "="){
            document.querySelector('.input').value=x.slice(0, -1);
            document.querySelector('.space').click();
        }
    }
    return <input onInput={check} className="input" type="text" style={{width:'80%', height:'50px'}} />
}
export default Input;