import "./Numbers.css";
import Input from "./Input";
import NumButtons from "./NumButton";
function Numbers(){
    return (
        <div className="numbers display" style={{flexWrap: 'wrap', justifyContent: 'space-around', flexDirection:'row-reverse'}}>
            <NumButtons id='9' number='9'/>
            <NumButtons id='8' number='8'/>
            <NumButtons id='7' number='7'/>
            <NumButtons id='6' number='6'/>
            <NumButtons id='5' number='5'/>
            <NumButtons id='4' number='4'/>
            <NumButtons id='3' number='3'/>
            <NumButtons id='2' number='2'/>
            <NumButtons id='1' number='1'/>
            <NumButtons id='0' number='0'/>
        </div>
    )
}
export default Numbers;

