import "./reset.css"
import Input from "./Input";
import Numbers from "./Numbers";
import Plus from "./Plus";
import Equals from "./Equals";
import Minus from "./Minus";
import Mult from "./Mult";
import Divide from "./Divide";
function Calculator(){
    function reset() {
        document.querySelector('.input').value = '';
    }
    return (
        <div className="calculator displayColumn" style={{width:'400px', height:'500px', backgroundColor:'grey', borderRadius:'20px'}}>
            <Input />
            <div  className="display" >
                <Numbers />
                <div className="displayColumn" style={{height:'350px' ,marginLeft:'20px', justifyContent: 'space-around', marginTop:'49px'}}>
                    <Equals />
                    <Plus />
                    <Minus />
                    <Mult />
                    <Divide />
                    <button onClick={reset} style={{backgroundColor:'orange', borderRadius:'20px',border:'none', cursor: '',width: '50px',height:'50px'}}>Reset</button>
                </div>
                
            </div>
        </div>
    )
}
export default Calculator;